.detail {
  margin-top: 40px;
  display: flex;
  justify-content: space-between;
}
.wrappe{
  width: 65%;
}
.detail__left {
  width: 100%;
  border: 2px solid red;
  background-color: rgba(150, 188, 204, 0.95);
  position: relative;
  border-radius: 7px;
}
.detail__right {
  width: 30%;
  border: 2px solid red;
  background-color: rgba(150, 188, 204, 0.95);
  border-radius: 5px;
}

.left__title {
  margin-top: 10px;
  text-align: center;
  color: rgb(218, 109, 95);
}

.left__img {
  margin-top: 20px;
  height: 450px;
  padding: 0 80px;
}
.left__genres {
  color: beige;
  text-align: center;
  font-size: 25px;
  margin-top: 20px;
}

.left__genres span {
  margin-left: 20px;
  color: rgb(205, 35, 35);
  font-size: 20px;
}
.left__desc {
  color: rgb(205, 35, 35);
  text-align: center;
  margin-top: 20px;

  font-size: 19px;
}

.left__desc span {
  margin-right: 20px;
  font-size: 25px;
  color: beige;
}

.left__show {
  margin-top: 20px;
  max-width: 100%;
  display: block;
  height: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: green;
  margin: 20px 20px 20px;
}

.right__wrapper {
  display: flex;
  justify-content: space-between;
  margin-top: 20px;
}

.right__img {
  width: 40%;
}
.right__desc {
  width: 55%;
}

.right__title {
  font-size: 14px;
}
