* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.container {
  max-width: 75%;
  margin: 0 auto;
}

.row {
  display: flex;
  flex-wrap: wrap;
}

.back-video {
  position: fixed;
  right: 0;
  bottom: 0;
  min-width: 100%;
  min-height: 100%;
  width: auto;
  height: auto;
  z-index: -1;
  background-size: cover;
}

a {
  text-decoration: none;
  color: white;
}

li {
  list-style: none;
}
img {
  width: 100%;
  height: 100%;
}
